.out {
	min-height: 320px;
	overflow: hidden; }
%inner {
	max-width: 1880px;
	margin: auto;
	padding: 0 15px; }
.inner {
	@extend %inner;
	position: relative;
	@include clr; }
.section1 {
	background-color: #7200d4;
	height: 100vh;
	min-height: 800px;
	.inner {
		height: 100%; } }
.logo {
	@include panton;
	font-size: 36px;
	line-height: 1;
	display: inline-block;
	vertical-align: middle; }
.entry {
	font-size: 0;
	&__item {
		display: inline-block;
		vertical-align: middle;
		+ .entry__item {
			margin-left: 40px; } }
	a {
		color: #fafafc;
		font-size: 18px;
		&:hover,
		&:focus {
			color: darken(#fafafc, 15%); } } }
.company {
	text-align: center;
	line-height: 1;
	max-width: 433px;
	&__logo {
		@include hide-text; }
	&__text {
		font-size: 32px;
		margin-top: 5px; }
	&__button {
		margin-top: 46px; }
	img {
		display: block;
		max-width: 100%;
		height: auto; } }
.columns {
	height: 100%;
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	align-items: center; }
