.list {
	&__item {
		&:not(:last-child) {
			margin-bottom: 22px; } }
	&__item_top-vertical {
		.list__item-icon,
		.list__item-text {
			vertical-align: top; } }
	&__item-icon,
	&__item-text {
		display: table-cell;
		vertical-align: middle; }
	&__item-text {
		padding-left: 20px;
		@include roboto-light;
		font-size: 18px;
		line-height: 1.2; } }
.board {
	padding: 7px 16px;
	background-color: #00aaff;
	text-transform: uppercase;
	@include gilroy;
	text-align: center;
	font-size: 18px;
	position: absolute;
	left: 0;
	bottom: 0;
	white-space: nowrap;
	transform: translate3d(-50%, -50%, 0);
	margin-bottom: 62px;
	&:before,
	&:after {
		content: '';
		display: block;
		position: absolute;
		top: 100%;
		left: 0;
		right: 0;
		margin: auto; }
	&:after {
		@include arr(18, 9, #00aaff, b); }
	&:before {
		width: 1px;
		height: 52px;
		background-color: #00aaff; }
	&_pink {
		background-color: #ff1799;
		&:after {
			@include arr(18, 9, #ff1799, b); }
		&:before {
			background-color: #ff1799; } } }
